'use client';

import { useState } from 'react';
import { Moon, Sun } from 'lucide-react';

// Waves Component
function Waves({ 
  lineColor = "#fff", 
  backgroundColor = "rgba(255, 255, 255, 0.2)", 
  waveSpeedX = 0.02, 
  waveSpeedY = 0.01, 
  waveAmpX = 40, 
  waveAmpY = 20, 
  friction = 0.9, 
  tension = 0.01, 
  maxCursorMove = 120, 
  xGap = 12, 
  yGap = 36 
}) {
  const canvasRef = React.useRef(null);
  const animationRef = React.useRef(null);
  const mouseRef = React.useRef({ x: 0, y: 0 });
  const timeRef = React.useRef(0);

  React.useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    let waves = [];

    const resizeCanvas = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      initWaves();
    };

    const initWaves = () => {
      waves = [];
      const cols = Math.floor(canvas.width / xGap) + 1;
      const rows = Math.floor(canvas.height / yGap) + 1;

      for (let i = 0; i < cols; i++) {
        waves[i] = [];
        for (let j = 0; j < rows; j++) {
          waves[i][j] = {
            x: i * xGap,
            y: j * yGap,
            baseX: i * xGap,
            baseY: j * yGap,
            vx: 0,
            vy: 0
          };
        }
      }
    };

    const updateWaves = () => {
      timeRef.current += 0.016;

      for (let i = 0; i < waves.length; i++) {
        for (let j = 0; j < waves[i].length; j++) {
          const point = waves[i][j];
          
          // Distance from mouse
          const dx = mouseRef.current.x - point.baseX;
          const dy = mouseRef.current.y - point.baseY;
          const distance = Math.sqrt(dx * dx + dy * dy);
          
          // Mouse influence (very subtle)
          const mouseInfluence = Math.max(0, maxCursorMove - distance) / maxCursorMove;
          const mouseForceX = (dx / distance || 0) * mouseInfluence * 0.5;
          const mouseForceY = (dy / distance || 0) * mouseInfluence * 0.5;
          
          // Wave motion
          const waveX = Math.sin(timeRef.current * waveSpeedX + point.baseX * 0.01) * waveAmpX * 0.3;
          const waveY = Math.cos(timeRef.current * waveSpeedY + point.baseY * 0.01) * waveAmpY * 0.3;
          
          // Target position
          const targetX = point.baseX + waveX + mouseForceX;
          const targetY = point.baseY + waveY + mouseForceY;
          
          // Spring physics (very smooth)
          const forceX = (targetX - point.x) * tension;
          const forceY = (targetY - point.y) * tension;
          
          point.vx += forceX;
          point.vy += forceY;
          point.vx *= friction;
          point.vy *= friction;
          
          point.x += point.vx;
          point.y += point.vy;
        }
      }
    };

    const drawWaves = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      
      // Draw background
      ctx.fillStyle = backgroundColor;
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      
      ctx.strokeStyle = lineColor;
      ctx.lineWidth = 0.5;
      ctx.globalAlpha = 0.3;
      
      // Draw horizontal lines
      for (let j = 0; j < waves[0]?.length; j++) {
        ctx.beginPath();
        for (let i = 0; i < waves.length; i++) {
          const point = waves[i][j];
          if (i === 0) {
            ctx.moveTo(point.x, point.y);
          } else {
            ctx.lineTo(point.x, point.y);
          }
        }
        ctx.stroke();
      }
      
      // Draw vertical lines
      for (let i = 0; i < waves.length; i++) {
        ctx.beginPath();
        for (let j = 0; j < waves[i].length; j++) {
          const point = waves[i][j];
          if (j === 0) {
            ctx.moveTo(point.x, point.y);
          } else {
            ctx.lineTo(point.x, point.y);
          }
        }
        ctx.stroke();
      }
    };

    const animate = () => {
      updateWaves();
      drawWaves();
      animationRef.current = requestAnimationFrame(animate);
    };

    const handleMouseMove = (e) => {
      mouseRef.current.x = e.clientX;
      mouseRef.current.y = e.clientY;
    };

    resizeCanvas();
    window.addEventListener('resize', resizeCanvas);
    window.addEventListener('mousemove', handleMouseMove);
    animate();

    return () => {
      window.removeEventListener('resize', resizeCanvas);
      window.removeEventListener('mousemove', handleMouseMove);
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
      }
    };
  }, [lineColor, backgroundColor, waveSpeedX, waveSpeedY, waveAmpX, waveAmpY, friction, tension, maxCursorMove, xGap, yGap]);

  return (
    <canvas
      ref={canvasRef}
      className="fixed inset-0 pointer-events-none z-0"
      style={{ width: '100vw', height: '100vh' }}
    />
  );
}

